<template>
  <v-hover v-slot="{ hover }">
    <v-card
      :elevation="hover ? 3 : 2"
      class="d-flex pa-2 mx-sm-auto flex-column"
      width="100%"
      max-width="500"
    >
      <div class="d-flex">
        <v-avatar color="red darken-3" size="64" rounded>
          <span class="white--text text-h5">{{ routeInfo.route }}</span>
        </v-avatar>
        <v-sheet class="flex-grow-1 d-flex flex-column align-stretch px-2">
          <v-sheet class="flex-grow-1 d-flex align-center text-left">
            <div class="d-flex flex-column">
              <span class="caption font-weight-light">{{
                $t("transport.to")
              }}</span>
              <v-icon>mdi-ray-start-arrow</v-icon>
            </div>
            <span class="text-h5 px-2"> {{ routeInfo.dest_tc }}</span>
          </v-sheet>
          <strong class="flex-grow-1">{{ stopInfo.name_tc }}</strong>
        </v-sheet>
      </div>
      <div v-if="validEta.length > 0">
        <div v-for="(etaItem, index) in validEta" :key="index">
          <v-sheet
            class="d-flex pa-2 mb-1 align-center"
            :elevation="etaItem.isArrived ? 0 : 7 - index * 3"
            :class="
              etaItem.isArrived
                ? 'grey'
                : $vuetify.theme.dark
                ? 'orange darken-' + (4 - index)
                : 'amber lighten-' + (index + 2)
            "
          >
            <div
              class="flex-grow-1 text-h5"
              :class="{
                'font-weight-medium': !etaItem.isArrived && index === 0,
                'font-weight-regular': index === 1,
                'font-weight-light': etaItem.isArrived || index > 1,
              }"
            >
              {{ etaItem.eta | arriveTime }}
            </div>
            <div class="font-weight-thin">{{ etaItem.rmk_tc }}</div>
          </v-sheet>
        </div>
      </div>
      <div v-else>
        <v-sheet
          class="d-flex pa-2 mb-1 align-center grey"
          :class="$vuetify.theme.dark ? 'darken-3' : 'lighten-2'"
          :elevation="2"
        >
          <div class="flex-grow-1 text-h5 font-weight-regular">未有班次</div>
        </v-sheet>
      </div>
    </v-card>
  </v-hover>
</template>

<script>
export default {
  name: "RouteStopEtaItem",
  props: {
    routeInfo: {
      type: Object,
      default: () => {},
    },
    stopInfo: {
      type: Object,
      default: () => {},
    },
    eta: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    validEta() {
      const now = new Date();
      const _eta = this.eta.filter((etaItem) => {
        return (
          this.stopInfo.bound === etaItem.dir &&
          parseInt(this.stopInfo.seq) === parseInt(etaItem.seq) &&
          etaItem.eta
        );
      });
      _eta.forEach((etaItem) => {
        etaItem.isArrived = new Date(etaItem.eta) < now;
      });
      return _eta;
    },
  },
  watch: {},
  methods: {},
};
</script>

