<template>
  <v-container>
    <v-row no-gutters>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-for="(item, index) in bookmarkStopsWithETA"
        :key="index"
      >
        <v-card class="mx-auto mb-2" max-width="344" outlined>
          <RouteStopEtaItem
            :routeInfo="item.routeInfo"
            :stopInfo="item.stopInfo"
            :eta="item.eta"
          />
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import RouteStopEtaItem from "@/components/RouteStopEtaItem.vue";
import { mapGetters } from "vuex";
import { apiKmbEta } from "@/common/api/kmb.transport.api";
export default {
  components: {
    RouteStopEtaItem,
  },
  computed: {
    ...mapGetters(["bookmarkStops"]),
    bookmarkStopsWithETA() {
      return this.bookmarkStops.map((x) => {
        const bookmarkStopInfo = x.stopInfo;
        const eta =
          this.etaList[
            `${bookmarkStopInfo.stop}-${bookmarkStopInfo.route}-${bookmarkStopInfo.service_type}`
          ];
        return {
          ...x,
          eta,
        };
      });
    },
  },
  data() {
    return {
      refreshEtaInterval: 10000,
      etaList: {},
    };
  },
  mounted() {
    this.getETAs();
    setInterval(() => {
      this.getETAs();
    }, this.refreshEtaInterval);
  },
  methods: {
    async getETAs() {
      for (let i = 0; i < this.bookmarkStops.length; i++) {
        const bookmarkStopInfo = this.bookmarkStops[i].stopInfo;
        const res = await apiKmbEta(
          bookmarkStopInfo.stop,
          bookmarkStopInfo.route,
          bookmarkStopInfo.service_type
        );
        // if(generated_timestamp)
        this.$set(
          this.etaList,
          `${bookmarkStopInfo.stop}-${bookmarkStopInfo.route}-${bookmarkStopInfo.service_type}`,
          res?.data
        );
      }
    },
  },
};
</script>

